@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

* {
  box-sizing: border-box;
  font-family: 'Raleway', sans-serif; }

.App {
  text-align: center;
  padding: 1px 50px 50px;
  background: #afdedc;
  min-height: 100vh;
  width: 100%; }

button {
  background: #91a8a4;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border: 1px solid #776871;
  color: #f0f7ee;
  font-size: large;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px auto 0; }

button:focus {
  outline: none; }

button:not(.disabled):hover {
  cursor: pointer;
  color: white; }

.info {
  color: white;
  font-style: italic;
  text-shadow: 1px 1px 2px black, 2px 2px #776871;
  text-transform: capitalize; }

::-webkit-scrollbar {
  width: 6px; }

/* scrollbar track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px #776871; }

/* scrollbar handle */
::-webkit-scrollbar-thumb {
  background: #f0f7ee; }

.form {
  background: #f0f7ee;
  border-radius: 5px;
  box-shadow: 0 0 5px #776871;
  color: #776871;
  padding: 10px 25px;
  max-width: 500px;
  margin: 0 auto 60px; }

.form > div {
  margin: 10px auto; }

.disabled {
  background: #f0f7ee;
  color: #776871;
  color: #999; }

button {
  background: #91a8a4;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border: 1px solid #776871;
  color: #f0f7ee;
  font-size: large;
  text-transform: capitalize; }

button:focus {
  outline: none; }

button:not(.disabled):hover {
  cursor: pointer;
  color: white; }

.name-display {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #776871;
  padding: 5px;
  color: #f0f7ee;
  border-radius: 5px 5px 0 0; }

.name-display > div {
  font-size: large;
  font-weight: bold;
  flex-grow: 1;
  padding-left: 5px;
  text-transform: capitalize; }

.edit-name {
  width: 30px;
  height: 30px;
  font-size: small;
  background-color: transparent;
  color: #f0f7ee; }

#cancel-button {
  background: transparent;
  border: none;
  color: #776871;
  position: absolute;
  right: 20px;
  top: 8px;
  width: 30px;
  height: 30px; }

#add-button {
  position: absolute;
  left: 50px;
  bottom: 20px;
  border-radius: 5px;
  border: none;
  box-shadow: 0 0 5px #776871; }

#add-button:not(.disabled):hover {
  cursor: pointer;
  color: white; }

.container {
  background: #91a8a4;
  padding: 0;
  border-radius: 5px 5px 0 0;
  position: relative;
  border: 1px solid #776871;
  box-shadow: 0 0 3px black;
  max-width: 1500px;
  margin: 0 auto; }

.grid-list {
  width: 100%; }

.gif {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%; }

.inset-button {
  height: 30px;
  width: 30px;
  margin-right: 5px; }

.inset-icon {
  color: #f0f7ee;
  font-size: small; }

.toolbar {
  background: #776871;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 5px 5px 0 0; }

.toolbar > .user-name {
  padding-left: 10px;
  color: #f0f7ee;
  font-weight: bold;
  font-size: large;
  text-transform: capitalize; }

#clear-all {
  margin: 0;
  background: transparent;
  border: none; }

.tile {
  text-transform: capitalize; }

h1 {
  font-family: 'Pacifico', cursive;
  color: #f0f7ee;
  text-shadow: 1px 1px 2px black, 2px 2px #776871;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.add-hobby {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.add-hobby > span {
  font-size: large;
  margin-right: 10px; }

@media only screen and (max-width: 500px) {
  .add-hobby > span {
    display: none; } }

.add-hobby-button {
  margin: 0; }

